import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Movies from '../views/Movies.vue'
import SingleMovies from '../views/SingleMovies.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home,
        meta: {
            title: '我不是青铜Q'
        }
    },
    {
        path: '/Movies',
        name: 'Movies',
        component: Movies,
        meta: {
            title: '我不是青铜Q'
        }
    },
    {
        path: '/SingleMovies/:id',
        name: 'SingleMovies',
        component: SingleMovies,
        meta: {
            title: '我不是青铜Q'
        }
    }
]

const router = new VueRouter({
    routes
})

export default router