<template>
  <div class="card">
      <div class="card-title">{{title}}</div>
      <div class="card-number"><span>{{currentNum}}</span>/{{allNum}}</div>
      <div class="card-progress">
          <div class="card-progress-num" :style="widthValue"></div>
      </div>
      <div class="card-Newest">
          <span>当日新增</span>
          <span>+{{newNum}}</span>
      </div>
      <pre>{{content}}</pre>
  </div>
</template>

<script>
export default {
    name:'Card',
    data:function(){
        return{
           content:''
        }
    },
    props:{
        title:{
            type:String,
            default:function(){
                return "没有填写标题";
            }
        },
        currentNum:{
            viladator:function(value){
                this.progress = "width:"+Math.floor(this.currentNum/100000*100) +"%";
                return value;
            }
        },
        allNum:{

        },
        newNum:{

        }
    },
    computed:{
        widthValue:function(){
            return "width:"+Math.floor(this.currentNum/100000*100) +"%";
        }
    }
}
</script>

<style>
.card{
    width: 100%;
    height: 160px;
    padding: 20px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
    color: #ffffff;
    margin-bottom: 20px;
}
.card div{
    margin-bottom: 15px;
}
.card .card-number span{
    font-size: 30px;
    padding-right: 10px;
}
.card .card-progress{
    width: 100%;
    height: 5px;
    background-color: hsla(0,0%,100%,.85);
    border-radius: 5px;
}
.card .card-progress .card-progress-num{
    width: 5%;
    height: 5px;
    background-color:#32CD32;
    border-radius: 5px;
}
.card .card-Newest{
    display: flex;
    justify-content: space-between;
}
</style>