<template>
  <div class="play">
    <iframe
      :src="srcUrl"
      scrolling="no"
      border="0"
      frameborder="no"
      framespacing="0"
      allowfullscreen="true"
    ></iframe>
    <div class="info">
      <h4>{{info.title}}</h4>
      <p>{{info.content}}</p>
        <span>Unity</span>
        <span>VR</span>
        <span>实验</span>
    </div>
  </div>
</template>
<script>
export default {
  props:['url','info'],
  data(){
    return{
      currentInfo:{}
    }
  },
  computed:{
    srcUrl:function(){
      return '//player.bilibili.com/player.html?' + this.url +'&page=1"';
    }
  }
};
</script>
